export default {
  data() {
    return {
      musicList: []
    }
  },
  // 过滤器
  filters: {
    formatArtists(value) {
      const temp = value.map(item => item.name)
      return temp.join('/')
    }
  },
  methods: {
    getMusicList() {
      uni.request({
        url: 'http://localhost:3000/personalized/newsong',
        success: ({ data }) => {
          if (data.code === 200) {
            this.musicList = data.result
          } else {
            uni.showToast({
              title: '获取数据失败',
              icon: 'none'
            })
          }
        }
      })
    },
    handleItem(id) {
    uni.navigateTo({
      url:`../song/song?id=${id}`
    })
    }
  },
  created() {
    this.getMusicList()
  }
}
